* {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  
  body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
  }

.shop {

    background: #d9dbf9;

    .card-title {
        color: 	#ff6f69;
    }

    padding-bottom: 8%;
    padding-top: 8%;
}

footer {

    background: #f9f7d9;
    
    .contact {

        .links {
           
            &:hover {
                font-weight: bold;
                color: #e84a5f;
              }
        }
    
        .contact_data {

            ul {
                li {
                    .fa {
                        color: #24a19c;

                        &:hover {
                            opacity: 1;
                        }
                    }
                }
            }

            a {
                text-decoration: none;

                &:hover {
                    font-weight: bold;
                    color: #e84a5f;
                }
            }

            .social_icons {
                margin-top: 30%;
            }
    
            .fa {
                padding: 15px;
                font-size: 20px;
                width: 50px;
                text-align: center;
                text-decoration: none;
                margin: 5px 2px;
                margin-bottom: 0;
                margin-top: 0;
                
                &:hover {
                    opacity: 0.7;
                }
            }
            
            .fa-facebook {
                background: #3B5998;
                color: white;
                
            }
            
            .fa-instagram {
                background: #E1306C;
                color: white;
            }
            
            .fa-linkedin {
                background: #007bb5;
                color: white;
            }
        }
    
    }

    h3 {
        padding-top: 7%;
        margin-bottom: 8%;
        text-align: center;
    }

    .footer-copyright {
        margin-top: 5%;
    }
}


